.greeting {
    width: 100%;
}

#greeting-banner {
    text-align: center;
    margin-top: 10%;

}

.list{
    margin-left: 10px;
    box-sizing: border-box;
    display: inline-block;
    float: none;
    vertical-align: top;
    margin-left: 1%;
    margin-bottom: 1em;
    position: relative;
    text-align: center;
    max-width: 300px;
}
.hrer {
    text-align: center;
    box-sizing: border-box;
    text-decoration: none;
}

#figur {  
    transform: scale(0.6);
    line-height: 1em;
    text-align: center;
    box-sizing: border-box;
    display: block;
    margin: 0;
    width: 100%;
    position: relative;
}

.span-p{
    line-height: 1.53846em;
    box-sizing: border-box;
    text-align: center;
    color: #242424;
    display: block;
    margin-bottom: 0em;
    font-size: 14px;    
}
.span-prine{
    line-height: 1.53846em;
    box-sizing: border-box;
    display: block;
    text-align: center;
    font-size: 14px;
    color: #2ca719e4;
}
.span-buy{
    line-height: 1.53846em;
    text-align: center;
    color: #000000;
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 400;
    margin-top: -0.4em;
}

.hrer:hover{
    text-decoration: underline #000000;
}



